    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.main-container {
    padding: var(--size-padding-small) var(--size-padding-medium);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: calc(100vw - 2 * var(--size-padding-medium));
    max-width: 100%;
}

.textarea {
    width: 100%;
    padding: var(--size-padding-medium);
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    margin: var(--size-margin-small) 0;
    background-color: var(--color-background-light);
    resize: vertical;
    min-height: 30px;
    overflow: auto;
}

.button {
    height: var(--size-element-height);
    padding: 0 var(--size-padding-large);
    border: none;
    border-radius: var(--size-border-radius);
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    white-space: nowrap;
    font-size: var(--size-font-default);
}

#compression-buttons {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

#compression-buttons .label {
    margin-right: var(--size-margin-small);
}

.compression-button {
    height: var(--size-element-height);
    margin-right: var(--size-margin-medium);
    padding: 0 var(--size-padding-large);
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    color: var(--color-text);
    border: none;
    cursor: pointer;
    transition: background-color var(--transition-speed);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.compression-button:hover {
    background-color: var(--color-background-dark);
}

.compression-button.selected {
    background-color: var(--color-background-active);
    color: var(--color-text-light);
}

.webpage-button {
    background-color: var(--color-button);
    color: var(--color-text-light);
}

.webpage-button:hover {
    background-color: var(--color-button-hover);
}

.horizontal-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin-top: var(--size-margin-medium);
}

.counter {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 var(--size-margin-small);
    flex-basis: auto;
}

.counter .label {
    margin-right: var(--size-margin-small);
}

.input {
    width: var(--width-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-large);
    border: none;
    border-radius: var(--size-border-radius);
    background-color: var(--color-background-medium);
    text-align: center;
    cursor: not-allowed;
    color: var(--color-text);
    transition: background-color var(--transition-speed);
    margin: 0 var(--size-margin-small);
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
}

.input:hover {
    background-color: var(--color-background-dark);
}

.json-types-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: var(--size-margin-medium);
}

.instructions {
    margin-bottom: var(--size-margin-medium);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    text-align: center;
}

.instructions p {
    font-size: var(--size-font-large);
    color: var(--color-text-muted);
    margin: 0;
}

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: var(--size-margin-medium) 0;
    width: 100%;
}

.box {
    height: var(--size-element-height);
    background-color: var(--color-button);
    color: var(--color-text-light);
    margin: var(--size-margin-small);
    display: inline-flex;
    align-items: center;
    padding: 0 var(--size-padding-medium);
    cursor: grab;
    user-select: none;
    border-radius: var(--size-border-radius);
    transition: background-color var(--transition-speed), transform 0.5s ease;
    line-height: var(--size-element-height);
    font-size: var(--size-font-default);
    position: relative;
}

.box:active {
    cursor: grabbing;
    background-color: var(--color-button-hover);
}

.box-content {
    display: flex;
    align-items: center;
}

.eye-icon {
    width: 20px;
    height: 20px;
    background-image: var(--icon-eye-open);
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 5px;
    display: none;
}

#row2 .box .eye-icon {
    display: inline-block;
}

.line {
    width: 100%;
    height: 2px;
    background-color: var(--color-border);
    margin: var(--size-margin-medium) 0;
}

.placeholder {
    height: var(--size-element-height);
    background-color: transparent;
    border: 2px dotted var(--color-border);
    margin: var(--size-margin-small);
    border-radius: var(--size-border-radius);
    padding: 0 var(--size-padding-medium);
}

.icon {
    display: inline-flex;
    align-items: center;
    margin-right: 0.5em;
    width: 1.2em;
    height: 1.2em;
}

.icon-file {
    background-image: var(--icon-file);
}

.icon-folder {
    background-image: var(--icon-folder);
}

.size-6 {
    width: 1.2em;
    height: 1.2em;
}

.filetype-input {
    width: var(--width-filetype-input);
    height: var(--size-element-height);
    padding: 0 var(--size-padding-medium);
    margin: var(--size-margin-medium) 0;
    border: 1px solid var(--color-border);
    border-radius: var(--size-border-radius);
    font-size: var(--size-font-default);
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: text;
    line-height: var(--size-element-height);
}

@media (max-width: 890px) {
    .horizontal-section {
        flex-direction: column;
        align-items: stretch;
    }

    .horizontal-section > * {
        margin-bottom: var(--size-margin-medium);
    }

    .json-types-container, .json-types-list {
        padding: 0 var(--size-padding-medium);
    }

    .main-container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .button, .input, .compression-button {
        width: 100%;
    }

    .filetype-input {
        width: calc(100% - 2 * var(--size-padding-medium));
    }
}

@media (max-width: 500px) {
    .button, .input, .compression-button, .textarea {
        padding: 0 var(--size-padding-medium);
    }

    .filetype-input {
        font-size: var(--size-font-default);
    }
